@media print {
  @page {
    margin: 0;
    padding: 0;
    size: auto;
  }
  body,
  #content,
  .container {
    margin: 0mm 0mm 0mm 0mm !important;
    padding: 0mm !important;
  }
  #header,
  #sidebar,
  #chat,
  .growl-animated,
  [data-action="print"] {
    display: none !important;
  }
  /* --------------------------------------------------------
        Invoice
    -----------------------------------------------------------*/
  .invoice {
    padding: 30px !important;
    -webkit-print-color-adjust: exact !important;
  }
  .invoice .card-header {
    background: #eee !important;
    padding: 20px;
    margin-bottom: 20px;
    margin: -60px -30px 25px -30px;
  }
  .invoice .page-header {
    display: none;
  }
  .invoice .highlight {
    background: #eee !important;
  }
}
/** Media - Overriding the Media object to 3.2 version in order to prevent issues like text overflow. **/
.media {
  margin-top: 0;
  overflow: visible;
}
.media:before,
.media:after {
  display: table;
  content: " ";
}
.media:after {
  clear: both;
}
.media:before,
.media:after {
  display: table;
  content: " ";
}
.media:after {
  clear: both;
}
